import { app, BrowserWindow, ipcMain, dialog } from 'electron'
import path from 'node:path'
import { fileURLToPath } from 'node:url'
import fs from 'node:fs'
import {
  initDatabase,
  closeDatabase,
  getDatabase,
  getCurrentDbPath,
  insertRecord,
  updateRecord,
  softDeleteRecord,
  getAllRecords,
  getRecordById,
  searchRecords,
  insertHistory,
  getRecordHistory,
  getAllRecordHistory,
  getStatistics,
  type Record
} from './database'

// Êï∞ÊçÆÁõÆÂΩï
const dataDir = path.join(process.cwd(), 'data')

// Á°Æ‰øùÊï∞ÊçÆÁõÆÂΩïÂ≠òÂú®
if (!fs.existsSync(dataDir)) {
  fs.mkdirSync(dataDir, { recursive: true })
}

// Ëé∑ÂèñÂΩìÂâçÊñá‰ª∂ÁõÆÂΩïÔºàES Ê®°ÂùóÂÖºÂÆπÔºâ
const __filename = fileURLToPath(import.meta.url)
const __dirname = path.dirname(__filename)

// The built directory structure
//
// ‚îú‚îÄ‚î¨‚îÄ‚î¨ dist
// ‚îÇ ‚îÇ ‚îî‚îÄ‚îÄ index.html
// ‚îÇ ‚îÇ
// ‚îÇ ‚îú‚îÄ‚î¨ dist-electron
// ‚îÇ ‚îÇ ‚îú‚îÄ‚îÄ main.js
// ‚îÇ ‚îÇ ‚îî‚îÄ‚îÄ preload.mjs
// ‚îÇ
process.env.APP_ROOT = path.join(__dirname, '..')

// üöß Use ['ENV_NAME'] avoid vite:define plugin - Vite@2.x
export const VITE_DEV_SERVER_URL = process.env['VITE_DEV_SERVER_URL']
export const MAIN_DIST = path.join(process.env.APP_ROOT, 'dist-electron')
export const RENDERER_DIST = path.join(process.env.APP_ROOT, 'dist')

process.env.VITE_PUBLIC = VITE_DEV_SERVER_URL ? path.join(process.env.APP_ROOT, 'public') : RENDERER_DIST

let win: BrowserWindow | null

function createWindow() {
  win = new BrowserWindow({
    icon: path.join(process.env.VITE_PUBLIC, 'images', 'logo.png'),
    width: 1445,
    height: 950,
    webPreferences: {
      preload: path.join(__dirname, 'preload.mjs'),
      contextIsolation: true,
      nodeIntegration: false
    },
  })

  // ÈöêËóèËèúÂçïÊ†è
  win.removeMenu()

  // Test active push message to Renderer-process.
  win.webContents.on('did-finish-load', () => {
    win?.webContents.send('main-process-message', (new Date).toLocaleString())
  })

  if (VITE_DEV_SERVER_URL) {
    win.loadURL(VITE_DEV_SERVER_URL).catch(error => {
      console.error('Failed to load URL:', error)
    })
  } else {
    // win.loadFile('dist/index.html')
    win.loadFile(path.join(RENDERER_DIST, 'index.html')).catch(error => {
      console.error('Failed to load file:', error)
    })
  }
}

// ËÆæÁΩÆ IPC Â§ÑÁêÜÂô®
function setupIpcHandlers() {
  // Ëé∑ÂèñÊâÄÊúâËÆ∞ÂΩï
  ipcMain.handle('db:getAllRecords', () => {
    try {
      return { success: true, data: getAllRecords() }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // Ê†πÊçÆIDËé∑ÂèñËÆ∞ÂΩï
  ipcMain.handle('db:getRecordById', (_, id: number) => {
    try {
      return { success: true, data: getRecordById(id) }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // ÊêúÁ¥¢ËÆ∞ÂΩï
  ipcMain.handle('db:searchRecords', (_, keyword: string) => {
    try {
      return { success: true, data: searchRecords(keyword) }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // ÊèíÂÖ•ËÆ∞ÂΩï
  ipcMain.handle('db:insertRecord', (_, record: Record) => {
    try {
      const id = insertRecord(record)
      return { success: true, data: { id } }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // Êõ¥Êñ∞ËÆ∞ÂΩïÔºà‰ΩøÁî®‰∫ãÂä°Á°Æ‰øùÊï∞ÊçÆ‰∏ÄËá¥ÊÄßÔºâ
  ipcMain.handle('db:updateRecord', (_, record: Record) => {
    const db = getDatabase()
    try {
      // ‰ΩøÁî®‰∫ãÂä°Á°Æ‰øùÊìç‰ΩúÂéüÂ≠êÊÄß
      const transaction = db.transaction(() => {
        // ÂÖàËé∑ÂèñÊóßËÆ∞ÂΩïÁî®‰∫éÂéÜÂè≤ËÆ∞ÂΩï
        const oldRecord = getRecordById(record.Id!)
        if (oldRecord) {
          insertHistory({
            RecordId: record.Id!,
            GuestName: oldRecord.GuestName,
            Amount: oldRecord.Amount,
            ItemDescription: oldRecord.ItemDescription,
            PaymentType: oldRecord.PaymentType,
            Remark: oldRecord.Remark,
            NewGuestName: record.GuestName,
            NewAmount: record.Amount,
            NewItemDescription: record.ItemDescription,
            NewPaymentType: record.PaymentType,
            NewRemark: record.Remark,
            OperationType: 'UPDATE',
            UpdateBy: 'System',
            ChangeDesc: 'Êõ¥Êñ∞ËÆ∞ÂΩï'
          })
        }
        updateRecord(record)
      })

      transaction()
      return { success: true }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // ËΩØÂà†Èô§ËÆ∞ÂΩïÔºà‰ΩøÁî®‰∫ãÂä°Á°Æ‰øùÊï∞ÊçÆ‰∏ÄËá¥ÊÄßÔºâ
  ipcMain.handle('db:softDeleteRecord', (_, id: number) => {
    const db = getDatabase()
    try {
      // ‰ΩøÁî®‰∫ãÂä°Á°Æ‰øùÊìç‰ΩúÂéüÂ≠êÊÄß
      const transaction = db.transaction(() => {
        // ÂÖàËé∑ÂèñÊóßËÆ∞ÂΩïÁî®‰∫éÂéÜÂè≤ËÆ∞ÂΩï
        const oldRecord = getRecordById(id)
        if (oldRecord) {
          insertHistory({
            RecordId: id,
            GuestName: oldRecord.GuestName,
            Amount: oldRecord.Amount,
            ItemDescription: oldRecord.ItemDescription,
            PaymentType: oldRecord.PaymentType,
            Remark: oldRecord.Remark,
            NewGuestName: null,
            NewAmount: null,
            NewItemDescription: null,
            NewPaymentType: null,
            NewRemark: null,
            OperationType: 'DELETE',
            UpdateBy: 'System',
            ChangeDesc: 'Âà†Èô§ËÆ∞ÂΩï'
          } as any)
        }
        softDeleteRecord(id)
      })

      transaction()
      return { success: true }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // Ëé∑ÂèñËÆ∞ÂΩïÂéÜÂè≤
  ipcMain.handle('db:getRecordHistory', (_, recordId: number) => {
    try {
      return { success: true, data: getRecordHistory(recordId) }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // Ëé∑ÂèñÊâÄÊúâÂéÜÂè≤ËÆ∞ÂΩï
  ipcMain.handle('db:getAllRecordHistory', () => {
    try {
      return { success: true, data: getAllRecordHistory() }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // Ëé∑ÂèñÁªüËÆ°Êï∞ÊçÆ
  ipcMain.handle('db:getStatistics', () => {
    try {
      return { success: true, data: getStatistics() }
    } catch (error) {
      return { success: false, error: (error as Error).message }
    }
  })

  // ÁîüÊàê PDF
  ipcMain.handle('app:generatePDF', async (_, data: {
    records: any[],
    appName: string,
    exportDate: string,
    theme?: {
      primary?: string
      paper?: string
      textPrimary?: string
      accent?: string
    }
  }) => {
    try {
      // Â§ÑÁêÜ records Êï∞ÁªÑÔºåÁ°Æ‰øùÂÖ∂‰∏≠ÁöÑÊØè‰∏™ÂØπË±°Âè™ÂåÖÂê´ÂèØÂ∫èÂàóÂåñÁöÑÂ±ûÊÄß
      const serializableData = {
        ...data,
        records: data.records.map((record: any) => ({
          guestName: record.guestName,
          amount: record.amount,
          amountChinese: record.amountChinese,
          itemDescription: record.itemDescription,
          paymentType: record.paymentType,
          remark: record.remark
        }))
      }

      // ÂàõÂª∫ÈöêËóèÁöÑÊâìÂç∞Á™óÂè£
      const printWindow = new BrowserWindow({
        width: 1200,
        height: 800,
        show: false,
        webPreferences: {
          nodeIntegration: true,
          contextIsolation: false
        }
      })

      // Á≠âÂæÖÈ°µÈù¢Âä†ËΩΩÂÆåÊàêÂπ∂Ê∏≤Êüì
      await new Promise<void>((resolve, reject) => {
        const timeout = setTimeout(() => {
          reject(new Error('ÊâìÂç∞È°µÈù¢Âä†ËΩΩË∂ÖÊó∂'))
        }, 10000)

        ipcMain.once('print-window-loaded', () => {
          try {
            // ÂèëÈÄÅÊï∞ÊçÆÂà∞ÊâìÂç∞Á™óÂè£
            printWindow.webContents.send('render-giftbook', serializableData)
          } catch (error) {
            console.error('ÂèëÈÄÅÊï∞ÊçÆÂà∞ÊâìÂç∞Á™óÂè£Â§±Ë¥•:', error)
            reject(new Error('ÂèëÈÄÅÊï∞ÊçÆÂà∞ÊâìÂç∞Á™óÂè£Â§±Ë¥•: ' + (error as Error).message))
          }
        })

        ipcMain.once('print-ready', () => {
          clearTimeout(timeout)
          resolve()
        })

        // Âä†ËΩΩÊâìÂç∞È°µÈù¢
        const printPagePath = path.join(process.env.VITE_PUBLIC as string, 'print.html')
        printWindow.loadFile(printPagePath).catch(reject)
      })

      // Á≠âÂæÖÂ≠ó‰ΩìÊ∏≤Êüì
      await new Promise(resolve => setTimeout(resolve, 500))

      // ÁîüÊàê PDF
      const pdfBuffer = await printWindow.webContents.printToPDF({
        margins: {
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
        },
        printBackground: true,
        landscape: true,
        pageSize: 'A4'
      })

      // ÂÖ≥Èó≠ÊâìÂç∞Á™óÂè£
      printWindow.close()

      // ÊòæÁ§∫‰øùÂ≠òÂØπËØùÊ°Ü
      const { filePath } = await dialog.showSaveDialog({
        title: '‰øùÂ≠ò PDF',
        defaultPath: `Á§ºÈáëÁ∞ø_${serializableData.exportDate.replace(/[Âπ¥ÊúàÊó•]/g, '')}.pdf`,
        filters: [
          { name: 'PDF Êñá‰ª∂', extensions: ['pdf'] }
        ]
      })

      if (filePath) {
        fs.writeFileSync(filePath, pdfBuffer)
        return { success: true, filePath }
      } else {
        return { success: false, error: 'Áî®Êà∑ÂèñÊ∂à‰øùÂ≠ò' }
      }
    } catch (error) {
      console.error('ÁîüÊàê PDF Â§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // ==================== ÂêØÂä®È°µÁõ∏ÂÖ≥ IPC Â§ÑÁêÜÂô® ====================

  // ÊâìÂºÄÊï∞ÊçÆÂ∫ìÊñá‰ª∂ÂØπËØùÊ°Ü
  ipcMain.handle('electron:openDatabaseFile', async () => {
    try {
      const { filePaths } = await dialog.showOpenDialog({
        title: 'ÈÄâÊã©Á§ºÈáëÁ∞øÊï∞ÊçÆÊñá‰ª∂',
        defaultPath: dataDir,
        filters: [
          { name: 'Êï∞ÊçÆÂ∫ìÊñá‰ª∂', extensions: ['db'] },
          { name: 'ÊâÄÊúâÊñá‰ª∂', extensions: ['*'] }
        ],
        properties: ['openFile']
      })

      if (filePaths && filePaths.length > 0) {
        return { success: true, filePath: filePaths[0] }
      } else {
        return { success: false, error: 'Áî®Êà∑ÂèñÊ∂àÈÄâÊã©' }
      }
    } catch (error) {
      console.error('ÊâìÂºÄÊñá‰ª∂ÂØπËØùÊ°ÜÂ§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // ÂàõÂª∫Êñ∞Êï∞ÊçÆÂ∫ì
  ipcMain.handle('electron:createNewDatabase', async (_, fileName: string) => {
    try {
      // ÁîüÊàêÊñ∞Êï∞ÊçÆÂ∫ìË∑ØÂæÑ
      const newDbPath = path.join(dataDir, fileName)
      
      // Â¶ÇÊûúÊñá‰ª∂Â∑≤Â≠òÂú®ÔºåÊ∑ªÂä†Â∫èÂè∑
      let finalPath = newDbPath
      let counter = 1
      while (fs.existsSync(finalPath)) {
        const ext = path.extname(fileName)
        const base = path.basename(fileName, ext)
        finalPath = path.join(dataDir, `${base}_${counter}${ext}`)
        counter++
      }

      // ÈáçÊñ∞ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ìÔºà‰º†ÂÖ•Êñ∞Ë∑ØÂæÑÔºå‰ºöÂàõÂª∫Êñ∞Êñá‰ª∂Ôºâ
      initDatabase(finalPath)

      return { success: true, filePath: finalPath }
    } catch (error) {
      console.error('ÂàõÂª∫Êñ∞Êï∞ÊçÆÂ∫ìÂ§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // ÂàáÊç¢Êï∞ÊçÆÂ∫ì
  ipcMain.handle('electron:switchDatabase', async (_, filePath: string) => {
    try {
      // Ê£ÄÊü•Êñá‰ª∂ÊòØÂê¶Â≠òÂú®
      if (!fs.existsSync(filePath)) {
        return { success: false, error: 'Êï∞ÊçÆÂ∫ìÊñá‰ª∂‰∏çÂ≠òÂú®' }
      }

      // ÈáçÊñ∞ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ìÔºà‰º†ÂÖ•ÊåáÂÆöË∑ØÂæÑÔºâ
      initDatabase(filePath)

      return { success: true }
    } catch (error) {
      console.error('ÂàáÊç¢Êï∞ÊçÆÂ∫ìÂ§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // ‰øùÂ≠òÂΩìÂâçÊï∞ÊçÆÂ∫ìÔºàÈáçÂëΩÂêçÔºâ
  ipcMain.handle('electron:saveCurrentDatabase', async (_, fileName: string) => {
    try {
      const dbPath = getCurrentDbPath()
      if (!dbPath || !fs.existsSync(dbPath)) {
        return { success: false, error: 'ÂΩìÂâçÊ≤°ÊúâÂèØ‰øùÂ≠òÁöÑÊï∞ÊçÆ' }
      }

      // ÁîüÊàêÊñ∞Ë∑ØÂæÑ
      const newPath = path.join(dataDir, fileName)

      // Â¶ÇÊûúÊñ∞Ë∑ØÂæÑ‰∏éÂΩìÂâçË∑ØÂæÑ‰∏çÂêåÔºåÂàôÈáçÂëΩÂêç
      if (newPath !== dbPath) {
        // Â¶ÇÊûúÁõÆÊ†áÊñá‰ª∂Â∑≤Â≠òÂú®ÔºåÊ∑ªÂä†Â∫èÂè∑
        let finalPath = newPath
        let counter = 1
        while (fs.existsSync(finalPath)) {
          const ext = path.extname(fileName)
          const base = path.basename(fileName, ext)
          finalPath = path.join(dataDir, `${base}_${counter}${ext}`)
          counter++
        }

        // ÂÖ≥Èó≠Êï∞ÊçÆÂ∫ìËøûÊé•
        closeDatabase()

        // ÈáçÂëΩÂêçÊñá‰ª∂
        fs.renameSync(dbPath, finalPath)

        // ÈáçÊñ∞ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ìÔºà‰ΩøÁî®Êñ∞Ë∑ØÂæÑÔºâ
        initDatabase(finalPath)

        return { success: true, filePath: finalPath }
      }

      return { success: true, filePath: dbPath }
    } catch (error) {
      console.error('‰øùÂ≠òÊï∞ÊçÆÂ∫ìÂ§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // Ëé∑ÂèñÊúÄËøëÊâìÂºÄÁöÑÊï∞ÊçÆÂ∫ìÂàóË°®
  ipcMain.handle('electron:getRecentDatabases', async () => {
    try {
      // Êâ´ÊèèÊï∞ÊçÆÁõÆÂΩï‰∏≠ÁöÑÊâÄÊúâ .db Êñá‰ª∂
      const files = fs.readdirSync(dataDir)
      const dbFiles = files
        .filter(file => file.endsWith('.db'))
        .map(file => {
          const filePath = path.join(dataDir, file)
          const stats = fs.statSync(filePath)
          return {
            name: path.basename(file, '.db'),
            path: filePath,
            lastOpened: stats.mtime.toISOString()
          }
        })
        .sort((a, b) => new Date(b.lastOpened).getTime() - new Date(a.lastOpened).getTime())

      return { success: true, recentDatabases: dbFiles }
    } catch (error) {
      console.error('Ëé∑ÂèñÊúÄËøëÊï∞ÊçÆÂ∫ìÂàóË°®Â§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // Âà†Èô§Êï∞ÊçÆÂ∫ìÊñá‰ª∂
  ipcMain.handle('electron:deleteDatabase', async (_, filePath: string) => {
    try {
      // Ê£ÄÊü•Êñá‰ª∂ÊòØÂê¶Â≠òÂú®
      if (!fs.existsSync(filePath)) {
        return { success: false, error: 'Êï∞ÊçÆÂ∫ìÊñá‰ª∂‰∏çÂ≠òÂú®' }
      }

      // Â¶ÇÊûúÂà†Èô§ÁöÑÊòØÂΩìÂâçÊâìÂºÄÁöÑÊï∞ÊçÆÂ∫ìÔºåÂÖàÂÖ≥Èó≠ËøûÊé•
      const currentDbPath = getCurrentDbPath()
      if (currentDbPath === filePath) {
        closeDatabase()
      }

      // Âà†Èô§Êñá‰ª∂
      fs.unlinkSync(filePath)

      return { success: true }
    } catch (error) {
      console.error('Âà†Èô§Êï∞ÊçÆÂ∫ìÂ§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })

  // ÊâìÂºÄÂØºÂÖ•Êñá‰ª∂ÂØπËØùÊ°ÜÔºàExcelÔºâ
  ipcMain.handle('electron:openImportFile', async () => {
    try {
      const { filePaths } = await dialog.showOpenDialog({
        title: 'ÈÄâÊã©Ë¶ÅÂØºÂÖ•ÁöÑ Excel Êñá‰ª∂',
        defaultPath: dataDir,
        filters: [
          { name: 'Excel Êñá‰ª∂', extensions: ['xlsx', 'xls'] },
          { name: 'ÊâÄÊúâÊñá‰ª∂', extensions: ['*'] }
        ],
        properties: ['openFile']
      })

      if (filePaths && filePaths.length > 0) {
        return { success: true, filePath: filePaths[0] }
      } else {
        return { success: false, error: 'Áî®Êà∑ÂèñÊ∂àÈÄâÊã©' }
      }
    } catch (error) {
      console.error('ÊâìÂºÄÂØºÂÖ•Êñá‰ª∂ÂØπËØùÊ°ÜÂ§±Ë¥•:', error)
      return { success: false, error: (error as Error).message }
    }
  })
}

// Quit when all windows are closed, except on macOS. There, it's common
// for applications and their menu bar to stay active until the user quits
// explicitly with Cmd + Q.
app.on('window-all-closed', () => {
  if (process.platform !== 'darwin') {
    closeDatabase()
    app.quit()
    win = null
  }
})

app.on('activate', () => {
  // On OS X it's common to re-create a window in the app when the
  // dock icon is clicked and there are no other windows open.
  if (BrowserWindow.getAllWindows().length === 0) {
    createWindow()
  }
})

app.whenReady().then(() => {
  try {
    // ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ì
    initDatabase()
    console.log('Database initialized successfully')
  } catch (error) {
    console.error('Failed to initialize database:', error)
  }
  // ËÆæÁΩÆ IPC Â§ÑÁêÜÂô®
  setupIpcHandlers()
  // ÂàõÂª∫Á™óÂè£
  createWindow()
})
